<template>
  <div class="content-tab">
    <div class="content-tab-left">
      <div
        class="tab-item-film"
        v-for="(item, index) in list"
        :key="index"
        :class="{ act: tabIndex == index }"
        @click="tabIndexAct1(item, index)"
      >
        <div>
          {{ item }}
        </div>
        <div class="tab-item-film-fenge"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
    },
  },

  data() {
    return {
      tabIndex: 0,
    };
  },
  methods: {
    tabIndexAct1(item, index) {
      this.tabIndex = index;
      this.$emit("tabIndexAct", {
        item,
        index,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.content-tab {
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-between;
  -webkit-user-select: none; /* Safari浏览器 */
  -moz-user-select: none; /* Firefox浏览器 */
  -ms-user-select: none; /* IE/Edge浏览器 */
  user-select: none; /* 标准语法 */
  .content-tab-left {
    display: flex;
    flex-wrap: wrap;
    .tab-item-film {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 25px;
      border-radius: 6px;
      font-size: 14px;
      margin-right: 40px;
      margin-bottom: 20px;
      cursor: pointer;
      div:nth-child(1) {
        margin-bottom: 4px;
      }
    }
    .tab-item-film.act {
      // background: #f4f4f4;
      color: #57090e;
      .tab-item-film-fenge {
        width: 26px;
        height: 4px;
        background: #57090e;
        border-radius: 1px;
      }
    }
  }
  .content-tab-right {
    height: 25px;
    display: flex;
    padding: 2px 10px;
    border-radius: 6px;
    .tab-item-right {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 44px;
      height: 25px;
      border-radius: 6px;
      font-size: 14px;
      cursor: pointer;
    }
    .tab-item-icon {
      color: #333333;
    }
  }
}
.content-tab.expand {
  height: auto;
  overflow: visible;
}
</style>
