import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import ELEMENT from "element-ui";
import './element-variables.scss'
import "element-ui/lib/theme-chalk/index.css";
import "element-ui/lib/theme-chalk/display.css";
import i18n from "./i18n";
// 轮播
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
// Vant
import { Icon } from 'vant';
//顶部页面加载条
import NProgress from "nprogress";
import "nprogress/nprogress.css";

import tabView from "@/components/tabView";
import filmLibraryList from "@/components/filmLibraryList";
import readList from "@/components/readList";
import shortFilmTab from "@/components/shortFilmTab";
import shortFilmList from "@/components/shortFilmList";
import upToDateList from "@/components/upToDateList";
import routeView from "@/components/routeView";

Vue.component("tab-view", tabView);
Vue.component("filmLibrary-list", filmLibraryList);
Vue.component("read-list", readList);
Vue.component("shortFilm-tab", shortFilmTab);
Vue.component("shortFilm-list", shortFilmList);
Vue.component("upToDate-list", upToDateList);
Vue.component("route-view", routeView);

router.beforeEach((to, from, next) => {
  NProgress.start();
  next();
});
//路由跳转结束
router.afterEach(() => {
  NProgress.done();
});
Vue.config.productionTip = false;
Vue.use(ELEMENT, {
  i18n: (key, value) => i18n.t(key, value), // 在注册Element时设置i18n的处理方法,可以实现当点击切换按钮后，elementUI可以自动调用.js语言文件实现多语言切换
});
Vue.use(VueAwesomeSwiper)
Vue.use(Icon);
new Vue({
  router,
  store,
  i18n,
  render: function (h) {
    return h(App);
  },
}).$mount("#app");
