<template>
  <div class="content-tab" :class="{ expand: isExpand }">
    <div class="content-tab-left">
      <div
        class="tab-item"
        v-for="(item, index) in list"
        :class="{ act: tabIndex == index }"
        @click="tabIndexAct(item, index)"
      >
        {{ item }}
      </div>
    </div>
    <div class="content-tab-right" :class="{ act: isExpand }" @click="goExpand">
      <div class="tab-item-right">{{ $t('head.more') }}</div>
      <div class="tab-item-icon">></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      default: 20,
    },
    isOne: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      isExpand: false,
      tabIndex: 0,
    };
  },
  mounted(){
    if(this.isOne){
      this.tabIndex = 1
    }
  },
  methods: {
    tabIndexAct(item, index) {
      if (this.isOne && index == 0) {
        return;
      }
      this.tabIndex = index;
      this.$emit("tabIndexAct", {
        item,
        index,
      });
    },
    goExpand() {
      this.isExpand = !this.isExpand;
    },
  },
};
</script>

<style lang="scss" scoped>
.content-tab {
  width: 100%;
  height: 30px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  -webkit-user-select: none; /* Safari浏览器 */
  -moz-user-select: none; /* Firefox浏览器 */
  -ms-user-select: none; /* IE/Edge浏览器 */
  user-select: none; /* 标准语法 */
  .content-tab-left {
    display: flex;
    flex-wrap: wrap;
    .tab-item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 68px;
      height: 25px;
      border-radius: 6px;
      font-size: 14px;
      margin-right: 40px;
      margin-bottom: 20px;
      cursor: pointer;
    }
    .tab-item.act {
      background: #f4f4f4;
      color: #57090e;
    }
  }
  .content-tab-right {
    height: 25px;
    display: flex;
    padding: 2px 10px;
    border-radius: 6px;
    .tab-item-right {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 25px;
      border-radius: 6px;
      font-size: 14px;
      cursor: pointer;
    }
    .tab-item-icon {
      color: #333333;
    }
  }
  .content-tab-right.act {
    background-color: #f4f4f4;
    color: #57090e;
    .tab-item-icon {
      color: #57090e;
    }
  }
}
.content-tab.expand {
  height: auto;
  overflow: visible;
}
</style>
