import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "@/layout/index.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Layout,
    redirect: "/home",
    children: [
      {
        path: "home",
        name: "Nome", // 首页
        component: () => import("@/views/home/index.vue"),
      },
      {
        path: "filmLibrary",
        name: "FilmLibrary", // 片库
        component: () => import("@/views/filmLibrary/index.vue"),
      },
      {
        path: "filmLibrary/playDetails",
        name: "PlayDetails", // 片库播放
        component: () => import("@/views/playDetails/index.vue"),
      },
      {
        path: "shortFilm",
        name: "ShortFilm", // 短片
        component: () => import("@/views/shortFilm/index.vue"),
      },
      {
        path: "shortFilm/shortVideo",
        name: "ShortVideo", // 短片播放
        component: () => import("@/views/shortVideo/index.vue"),
      },
      {
        path: "read",
        name: "Read", // 读书
        component: () => import("@/views/read/index.vue"),
      },
      {
        path: "read/readDetails",
        name: "ReadDetails", // 读书
        component: () => import("@/views/readDetails/index.vue"),
      },
      {
        path: "audioFrequency",
        name: "AudioFrequency", // 音频
        component: () => import("@/views/audioFrequency/index.vue"),
      },
      {
        path: "audioFrequency/audioFrequencyInfo",
        name: "audioFrequencyInfo", // 音频详情
        component: () => import("@/views/audioFrequencyInfo/index.vue"),
      },
      {
        path: "upToDate",
        name: "UpToDate", // 最新
        component: () => import("@/views/upToDate/index.vue"),
      },
      {
        path: "upToDate/upToDateDetails",
        name: "UpToDateDetails", // 最新
        component: () => import("@/views/upToDateDetails/index.vue"),
      },
      {
        path: "myInfo",
        name: "MyInfo", // 我的
        component: () => import("@/views/myInfo/index.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
